// toastSuccess();
function toastSuccess(msg, duration = 3000) {
    console.log(duration);
    notify(msg, duration, "Toastify__toast--success")();
}
function toastError(msg, duration = 3000) {
    console.log(duration);
    notify(msg, duration, "Toastify__toast--error")();
}
function toastInfo(msg, duration = 3000) {
    console.log(duration);
    notify(msg, duration, "Toastify__toast--info")();
}

